import React, { Component } from "react";

class InputNumber extends Component {
  constructor(props) {
    super(props);

    this.handleUserInput = this.handleUserInput.bind(this);
    this.handleReset = this.handleReset.bind(this);
  }
  state = {};
  handleUserInput(e) {
    e.preventDefault();
    var userNumber = btoa(this.userNumber.value);
    this.userNumber.value = "";
    this.props.compareUserInput(userNumber);
  }
  handleReset() {
    this.props.onReset();
  }
  render() {
    if (this.props.wrong < 3) {
      return (
        <form className="app__input" onSubmit={this.handleUserInput}>
          Number is:
          <input
            type="text"
            pattern="[0-9]+"
            ref={ref => (this.userNumber = ref)}
            required="required"
            autoFocus="autoFocus"
          />
          <br />
        </form>
      );
    } else {
      return (
        <div className="app__end">
          <div className="app__notify">GGWP! Better luck next time (✧ω✧)</div>
          <br />
          <br />
          <button onClick={this.handleReset}>Restart</button>
        </div>
      );
    }
  }
}

export default InputNumber;
