import React, { Component } from "react";
import GenNumber from "./genNumber";
import InputNumber from "./inputNumber";

class GameApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      question: btoa(this.randomGenerate(2)),
      level: {
        main: 1,
        sub: 1
      },
      wrong: 0
    };
    this.compareUserInput = this.compareUserInput.bind(this);
    this.randomGenerate = this.randomGenerate.bind(this);
    this.resetState = this.resetState.bind(this);
  }
  state = {};
  resetState() {
    this.setState({
      question: btoa(this.randomGenerate(2)),
      level: {
        main: 1,
        sub: 1
      },
      wrong: 0
    });
  }
  randomGenerate(digit) {
    var max = Math.pow(10, digit) - 1;
    var min = Math.pow(10, digit - 1);

    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  compareUserInput(userNumber) {
    var currQuestion = this.state.question;
    var mainLevel = this.state.level.main;
    var subLevel = this.state.level.sub;
    var wrong = this.state.wrong;
    var digit = undefined;

    if (userNumber === currQuestion) {
      if (subLevel < 3) {
        ++subLevel;
      } else if (subLevel === 3) {
        ++mainLevel;
        subLevel = 1;
      }
    } else {
      ++wrong;
    }
    digit = mainLevel + 2;

    this.setState({
      question: btoa(this.randomGenerate(digit)),
      level: {
        main: mainLevel,
        sub: subLevel
      },
      wrong: wrong
    });
  }
  render() {
    return (
      <div className="main__app">
        <GenNumber
          question={this.state.question}
          level={this.state.level}
          wrong={this.state.wrong}
        />
        <InputNumber
          compareUserInput={this.compareUserInput}
          wrong={this.state.wrong}
          onReset={this.resetState}
        />
      </div>
    );
  }
}

export default GameApp;
