import React, { Component } from "react";
import "./publish.css";
import SignIn from "../images/ic_sign_in.png";
import Revelation from "../images/ic_revelation.png";
import Post from "../images/ic_post.png";
import Group from "../images/ic_group.png";
import Broadcast from "../images/ic_broadcast.png";
import Talk from "../images/ic_talk.png";
import Picture from "../images/ic_picture.png";
import Blog from "../images/ic_blog.png";

const menus = [
  { icon: SignIn, title: "去签到" },
  { icon: Revelation, title: "要爆料" },
  { icon: Post, title: "发帖子" },
  { icon: Group, title: "逛圈子" },
  { icon: Broadcast, title: "发广播" },
  { icon: Talk, title: "写说说" },
  { icon: Picture, title: "看美图" },
  { icon: Blog, title: "写日志" }
];

class Publish extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  state = {};
  render() {
    return (
      <div>
        <div className="publish_div" style={this.props.styleValue}>
          <div className="calendar">
            <div className="date-wrap">
              <li className="left">
                <span className="day">07</span>
              </li>
              <li className="middle">
                <p className="weekday">星期三</p>
                <p className="division">/</p>
                <p className="yearmonth">
                  <span className="year">
                    <i className="year_num" />
                    2019年
                  </span>
                  <span className="month">
                    <i className="month_num" />
                    06月
                  </span>
                </p>
              </li>
              <li className="right">
                <span className="lunar">
                  <i className="lunar_month">初</i>
                  <i className="lunar_day">三十</i>
                </span>
              </li>
            </div>
          </div>
          <div className="advertisement">
            <a href="#/">
              <em>推广</em>
              <img src="" alt="" width="100%" />
            </a>
          </div>
          <div className="publist_nav">
            <ul>
              {menus.map((menu, index) => {
                return (
                  <li key={index}>
                    <a href="#/">
                      <img alt="" src={menu.icon} />
                      <p>{menu.title}</p>
                    </a>
                  </li>
                );
              })}
            </ul>
          </div>
          <button className="close">
            <i className="iconfont" />
          </button>
        </div>
        <div
          className="publish_bg sharebg-active"
          style={{ visibility: "hidden" }}
        />
      </div>
    );
  }
}

export default Publish;
