import React, { Component } from "react";

class GenNumber extends Component {
  state = {};
  componentDidUpdate() {
    var time = undefined;
    var digit = undefined;

    // increase showing time depend on level
    digit = this.props.level.main + 2;
    time = 100 * Math.min(digit, 5) + 400 * Math.max(digit - 5, 0);

    var number = document.getElementById("number");
    setTimeout(function() {
      number.innerHTML = number.innerHTML.replace(/\w/gi, "·");
    }, time);
  }
  componentDidMount() {
    var number = document.getElementById("number");
    setTimeout(function() {
      number.innerHTML = number.innerHTML.replace(/\w|\W/gi, "·");
    }, 1200);
  }
  render() {
    return (
      <div className="app__gen-number">
        <div className="app__info">
          <p className="app__level">
            Level: {this.props.level.main} - {this.props.level.sub}
          </p>
          <p className="app__wrong">Wrong: {this.props.wrong}/3</p>
        </div>
        <p className="app__divider">############################</p>
        <p className="app__number" id="number">
          {this.props.wrong < 3 ? atob(this.props.question) : "????"}
        </p>
        <p className="app__divider">############################</p>
      </div>
    );
  }
}

export default GenNumber;
