import React, { Component } from "react";
import "./coreBall.css";
import arrow from "../../images/arrow.png";
import btn_fw from "../../images/btn_fw.png";

class CoreBall extends Component {
  state = {};
  componentDidMount() {
    var script = document.createElement("script");
    script.type = "text/javascript";
    script.async = true;
    script.src = "";
    document.head.appendChild(script);
  }
  render() {
    return (
      <div>
        <canvas style={{ display: "none" }} id="stage" />
        <div id="begin">
          <section className="center-box">
            <span className="title">Core Ball</span>
            <div className="button">
              <div className="txtAr" style={{ marginTop: 5 }} />
              <div className="text" />
            </div>
            <div className="op">
              <a id="btnFW" href="#/cordBall">
                <img alt="" src={btn_fw} />
              </a>
              <span
                id="btnReset"
                style={{
                  display: "inline-block",
                  marginLeft: 30,
                  color: "#666"
                }}
              >
                RESET
              </span>
              <div
                id="tip"
                style={{
                  fontSize: 14,
                  color: "#555",
                  marginTop: 15,
                  display: "none"
                }}
              >
                关卡重置完毕
              </div>
            </div>
          </section>
          <section id="wxArrow" style={{ display: "none" }}>
            <div
              style={{
                position: "absolute",
                width: "100%",
                height: "100%",
                opacity: 0.7,
                backgroundColor: "#000"
              }}
            />
            <img
              alt=""
              style={{ position: "absolute", right: 25 }}
              src={arrow}
            />
          </section>
        </div>
      </div>
    );
  }
}

export default CoreBall;
